.container {
position: relative;
text-align: center;
color: white;
margin: 60px;
}



.centered {
position: absolute;
top: 50%;
left: 50%;
-webkit-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
  font-family: Abel;
text-align: center;
font-size: 48px;
letter-spacing: 0;
    color: rgba(255, 255, 255, 1);
width: 353px;
}
.centeredDate{
color: black;
top: -3%;
font-size: 23px;
left: 50%;
position: absolute;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-family: Abel;
text-align: center;
font-size: 25px;
letter-spacing: 0;
width: 353px;

}

@media only screen and (max-width: 600px) {
  .container {
    margin: 33px;
    top: 21px;
    }


    .centered {
      top: 50%;
      left: 50%;
      font-size: 24px;
      width: 100px;
      }

      .adaviceImg {
        width: 300px;
        height: 400px;
    }
}
